body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #17252A;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth;
}

.intro-view{
    height: 70%;
    width: 100%;
    display: flex;
}

.menu-item{
    font-size: 8vw;
    color: #17252A;
    font-family: 'Poppins', sans-serif;
    cursor: pointer;
    -webkit-transition: all 0.7s linear;
    transition: all 0.7s linear;
    position: absolute;
    text-decoration: none;
}

.menu-item:hover{
    color: #DEF2F1;
    padding-left: .7em;
}

.menu-container{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 60%;
    top: 4%;
}

.menu-container > div{
    padding-left: 35%;
    height: 25%;
    width: 65%;
}

.menu-container > div:first-child{
    padding-top: 0 !important;
}

.back-color{
    background: #3AAFA9;
}

.main-img-container{
    height: 100%;
    width: 40%;
    display: grid;
    align-content: center;
}

.main-img-container>img{
    /*height: 110%;*/
    width: 100%;
}

.complement-intro{
    height: 15%;
    width: 100%;
    display: flex;
    color: #17252A;
}

.header-intro{
    width: 50%;
    height: 100%;
}

.header-intro-lg{
    width: 50%;
    height: 100%;
    text-align: center;
    font-family: 'Raleway', sans-serif;
    /*font-size: 1.3vw;*/
    font-size: 100%;
    color: #17252A;
}
.container-intro-lg{
    padding-top: calc(100vh*.03);
}
.container-intro-lg > span{
    padding-right: 2vw;
    cursor: pointer;
}

.title-intro{
    font-family: 'Raleway', sans-serif;
    font-size: 100%;
}

.title-container{
    padding-left: 2vw;
    padding-right: 2vw;
    padding-top: 3vh;
}

.footer-container{
    padding-left: 2vw;
    padding-right: 2vw;
    padding-top: 0 !important;
    position: absolute;
    top: 50%;
}

.footer-intro{
    width: 50%;
    height: 100%;
    position: relative;
    color: #17252A;
}
.title-container-body>span{
    font-size: 2.5vw;
    color:#17252A;
    font-family: 'Poppins', sans-serif;
}

.title-about-container{
    height: 100%;
    width: 40%;
    text-align: right;
    text-justify: inter-word;
    display: flex;
    flex-direction: column;
}

.text-title-about{
    font-size: 3vw;
    color:#17252A;
    font-family: 'Raleway', sans-serif; 
    padding: 8% 0% 0 10%;
    width: 90%;
    font-weight: 600;
}

.title-summary-container{
    height: 80%;
    text-align: left;
    -webkit-column-count: 2;
    -moz-column-count: 2;
    column-count: 2;
    -webkit-column-gap: 3vw;
    -moz-column-gap: 20px;
    grid-column-gap: 2vw;
    -webkit-column-gap: 2vw;
       -moz-column-gap: 2vw;
            column-gap: 2vw;
    padding: 3% 7% 0 5%;
    -webkit-column-fill: auto;
       -moz-column-fill: auto;
            column-fill: auto;
}

.title-summary-container>span{
    font-size: 1.3vw;
    color:#1d4959;
    font-family: 'Raleway', sans-serif; 
    line-height: 4vh;
    letter-spacing: .05em;
    margin: 0;
    font-weight: 600;
}

.img-summary{
    height: 70%;
}

.download-sum{
    float: right;
    margin: -10% 10% 0 0;
}

.col-summ-1{
    width: 60%;
}
@media (max-width: 1200px) {
    .about-body {
        flex-direction: column;
    }
    .text-title-about{
        font-size: 1.3rem;
        padding: 3% 0 0 0;
    }
    .title-summary-container{
        width: 90%;
        height: 100%;
        -webkit-column-count: auto;
           -moz-column-count: auto;
                column-count: auto;
        -webkit-column-count: initial;
           -moz-column-count: initial;
                column-count: initial;
        padding: 3% 5% 0 5%;
        display: grid;
    }
    .title-about-container{
        width: 100%;
        height: 10%;
        align-items: center;
    }
    .title-summary-container > span{
        font-size: 100%;
        white-space: break-spaces;
        height: 100%;
        overflow: auto;
    }
    .img-summary{
        display: none;
    }
    .title-container-about{
        display: grid;
        align-content: center;
        justify-content: center;
    }
    .col-summ-1{
        width: 100%;
        height: 100%;
    }
    .title-container-body>span{
        font-size: 5vw;
    }
}
.header-tech-section{
    /*background: linear-gradient(90deg, #DEF2F1 45%, #17252A 45%);*/
    background: #17252A;
}

.header-tech-section > span{
    color: #DEF2F1;
}

.elements-t-container{
    height: 100%;
}

.tech-item{
    width: 50px;
    height: 50px;
    margin: 2em;
    cursor: pointer;
}

.tech-item > img{
    height: 4em;
    -webkit-transition: all 250ms linear;
    transition: all 250ms linear;
}

.tech-item > img:hover{
    height: 6em;
}

.tech-container{
    width: 35em;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    height: 75%;
    padding-top: 5em;
    padding-left: 2em;
}

.tech-charts{
    width: 100%;
    background:#17252A;
    height: 100%;
    right: 0;
}

@media (max-width: 1200px) {
    .tech-charts{
        display: flex;
        flex-direction: column;
    }

    .tech-container{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        height: 100%;
        padding-top: 0;
        padding-left: 0;
        overflow-y: auto;
        overflow-x: hidden;
    }
}
.single-chart {
    width: 50%;
    text-align: center;
    height: auto;
    float: left;
    padding-top: 13%;
  }
  
.circular-chart {
  max-width: 100%;
  max-height: 100%;
  fill: #DEF2F1;
}

.circle-bg {
  fill: none;
  stroke: #132c35;
  stroke-width: 2.8;
}

.circle {
  fill: none;
  stroke-width: 2.8;
  stroke-linecap: round;
}

@-webkit-keyframes progress {
  0% {
    stroke-dasharray: 0 100;
  }
}

@keyframes progress {
  0% {
    stroke-dasharray: 0 100;
  }
}

.chart-value {
  font-size: 0.5em;
  text-anchor: middle;
}

.chart-type {
  font-size: 0.3em;
  text-anchor: middle;
}

.chart-descr{
  font-size: 0.2em;
  text-anchor: middle;
}

@media (max-width: 1200px) {
  .single-chart{
      width: 100%;
  }
}
.header-work-section{
    /*background: linear-gradient(90deg, #DEF2F1 65%, #DEF2F1 65%);*/
    color: #DEF2F1;
}

.header-work-section > span{
    color: #DEF2F1;
}
/*STYLES OF PRINCIPAL SITE*/
.big-view-site{
    width: 65%;
    height: 100%;
    display: flex;
    flex-direction: column;
    color: #DEF2F1;
}

.big-view-title{
    display: flex;
    align-items: center;
    justify-content: left;
    height: 100%;
    padding: 0 0 0 3%;
    font-size: 2em;
    font-family: 'Montserrat', sans-serif;
    color: #3AAFA9;
}

.big-view-desc-container{
    display: flex;
    height: 100%;
}

.big-view-desc{
    width: 94%;
    padding: 0 3% 0 3%;
    font-family: 'Raleway', sans-serif;
    font-size: 1.2em;
}

.btn-explore{
    width: 6em;
    height: 3em;
    background-color: #3AAFA9;
    border: none;
    text-transform: uppercase;
    color: #DEF2F1;
    font-weight: bolder;
    cursor: pointer;
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
}

.btn-explore:hover{
    background-color: #3c8581;
}

/*STYLES FO LIST SITES*/

.list-site-section-container{
    width: 90%;
    height: 100%;
    z-index: 1;
    opacity: 1;
    padding: 0 5% 0 5%;
}
.animation-list-section{
    -webkit-animation: navigation-sites 300ms linear;
            animation: navigation-sites 300ms linear;
}

.list-site-section{
    height: 95%;
    color: #DEF2F1;
    position: relative;
}

.navigation-list-button{
    width: 14%;
    position: absolute;
    cursor: pointer;
    color: #ffffff0d;
    -webkit-transition: all 250ms linear;
    transition: all 250ms linear;
    z-index: 2;
    height: 100%;
    display: grid;
    justify-content: center;
    align-items: center;
}

.navigation-list-button:hover{
    color: #ffffff;
}

.img-display{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: 0 0;
}

/* STYLE OF CARDS LIST */
.card-site{
    margin: 2% 5% 0 5%;
}

.img-card{
    object-fit: cover;
    width: 100%;
}

.card-container{
    width: 30%;
    float: right;
    display: grid;
    justify-content: center;
    align-items: center;
    margin: 0 0 0 7%;
}

.title-card{
    font-family: 'Poppins', sans-serif;
    font-size: 1.3em;
}

.summary-card{
    text-overflow: ellipsis;
    overflow: hidden;
    font-family: 'Raleway', sans-serif;
    white-space: nowrap;
}

.img-big-work{
    height:62%;
    padding:3% 0 3% 0;
}

.cntr-big-view-title{
    height:10%;
}

.container-btn-go{
    height: 25%;
}

.carou-work{
    width: 35%;
}

@-webkit-keyframes navigation-sites {
    0% {
       opacity: 0;
    }
    100% {
       opacity: 1;
    }
 }

@keyframes navigation-sites {
    0% {
       opacity: 0;
    }
    100% {
       opacity: 1;
    }
 } 

 @media (max-width: 1200px) {
    .body-work-section{
        flex-direction: column;
    }
    .img-big-work{
        display: none;
    }
    .cntr-big-view-title{
        height:50%;
    }
    .container-btn-go{
        height: 50%;
    }
    .big-view-site{
        width: 100%;
        height: 35%;
    }
    .carou-work{
        width: 100%;
        height: 65%;
    }
    .single-chart{
        padding: 0;
        height: 50%;
    }
    .big-view-desc{
        font-size: 2vw;
    }
    .card-site{
        margin: 1% 3% 0 3%;
    }
}

@media (max-width: 450px) {
    .big-view-desc{
        font-size: 4vw;
    }
}
.header-contact-section{
    height: 10%;
}
.header-contact-section>span{
